import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { FormControl, ValidationErrors } from '@angular/forms';
import { IssueService } from '../../issue.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  @Input() errors: ValidationErrors;
  token = '';
  userInfor = {
    user_name: '',
    password: ''
  }

  constructor(private router: Router, private issueService: IssueService, private cookieService: CookieService) { }

  ngOnInit() {
    if(this.cookieService.get('access_token').length > 0) {
      this.router.navigate(['/admin/dashboard'])
    }
  }

  SignIn() {
    if(this.userInfor.user_name.length > 0 && this.userInfor.password.length >= 6) {
      this.issueService.handle_login(this.userInfor).subscribe((issue) => {
        if(issue["status"] == 200) {
          this.cookieService.set("access_token", issue["token"], 0.02);
          this.cookieService.set("full_name", issue["user"].full_name, 0.02);
          this.cookieService.set("_id", issue["user"]._id, 0.02);
          this.cookieService.set("email", issue["user"].email, 0.02);
          this.router.navigate(['/admin/dashboard']);
        }
      })
    }
    
  }
}
