import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
import { IssueService } from '../../issue.service';

@Component({
  selector: 'app-adminpage',
  templateUrl: './adminpage.component.html',
  styleUrls: ['./adminpage.component.css'],
  host: {
    "(window:click)": "onClick()"
  }
})
export class AdminpageComponent implements OnInit {
  user = '';
  token = '';
  toogle_info = true;
  is_selected = false;
  constructor(private router: Router, private cookieService: CookieService, private issueService: IssueService) { }

  ngOnInit() {
    this.token = this.cookieService.get('access_token');
    this.check_is_login();
  }


  check_is_login() {
    if(this.token.length == 0) {
      this.router.navigate(['/login']);
    } else {
      this.issueService.verify_token(this.token).subscribe((issue) => {
        if(issue['state']) {
          this.user = this.cookieService.get('full_name');

          console.log(this.user);
          return this.router.navigate(['/admin/dashboard'])
        } else {
          this.cookieService.delete('access_token');
        }
        return this.router.navigate(['/login']);
      });
    }
  }


  showInforUser($event) {
    $event.stopPropagation();
    this.toogle_info = !this.toogle_info;
  }

  onClick() {
   this.toogle_info = true;
  }

  log_out() {
    this.cookieService.delete('access_token');
    this.router.navigate(['/login']);
  }
}
