import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LandingPageComponent} from '../app/components/landing-page/landing-page.component';
import { LoginComponent} from '../app/components/login/login.component';
import { SignupComponent } from '../app/components/signup/signup.component';
import { ForgotpasswordComponent} from '../app/components/forgotpassword/forgotpassword.component';
import { ResetpasswordComponent } from '../app/components/resetpassword/resetpassword.component';
import { AdminpageComponent } from '../app/components/adminpage/adminpage.component';
import { ErrorpageComponent } from '../app/components/errorpage/errorpage.component';

const routes: Routes = [
  {path: '', component: LandingPageComponent},
  {path: 'login', component: LoginComponent},
  {path: 'register', component: SignupComponent},
  {path: 'forgot-password', component: ForgotpasswordComponent},
  {path: 'reset-password', component: ResetpasswordComponent},
  {
    path: 'admin', 
    component: AdminpageComponent,
    children:[
      {path : '', loadChildren: './components/adminpage/adminpage.module#AdminpageModule'}
    ]
  }, 
  {path: '**', component: ErrorpageComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
